	module.exports = {"data":{"wordpressWpLessons":{"id":"00b2bbe8-8d6b-590b-924e-7baa0ce2786e","wordpress_id":1118,"title":"Gimp: The Basics","slug":"gimp-the-basics","content":"<h2>Overview and Goals</h2>\n<p>Gimp is a powerful tool that will help you understand the basic elements of graphic design. With the skills you acquire here you will be able to get started designing different things in Gimp.</p>\n<p>This lesson will be a mixture of a walkthrough on Learn, direct instruction, and project practice.</p>\n<h3>Goals:</h3>\n<ul>\n<li>Become familiar with the Gimp workspace</li>\n<li>Become familiar with the different menus and bars</li>\n<li>Begin to develop a Gimp workflow</li>\n<li>Understand the basic functions of the tools</li>\n</ul>\n<hr />\n<h2>Gimp&#8217;s Interface</h2>\n<p>The best way to refine your graphic design skills is to practice! The instruction will be kept to the minimum amount to enable you to begin creating.</p>\n<p>Gimp&#8217;s interface is similar to Adobe Photoshop, so many of the skills and workflow habits that you develop now will help you if you transition into Photoshop at a later date.</p>\n<h3>Interface Overview</h3>\n<p>Here is a basic walkthrough of the most important parts of Gimp&#8217;s interface that will help you get started. Remember, much of this is covered in the direct instruction, but here&#8217;s some images and resources if you need to review.</p>\n<ol>\n<li>The <strong>Toolbox</strong>(labeled as #1) is where your tools are contained. All the tools for selection, image manipulation (cropping, rotating, etc.), drawing, and more are all in this window.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_GD_Gimp_-_Gimp_1-600px1.jpg\" alt=\"MF-Gimp-1\" /></li>\n<li>To create a new image, navigate to the <strong>File</strong> menu and select <strong>New</strong>. You&#8217;ll then be able to change the image size and choose options for the image background. In this example, the <strong>Fill with</strong> is set to <strong>Transparent</strong>. This is important for creating pixel art. If you select white, your characters, logos, and other assets will have a solid white background.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_GD_Gimp_-_Gimp_2-600px2.jpg\" alt=\"MF-Gimp-2\" /></li>\n<li>This is an example of a new document created with the settings from the above image. The checkered background is transparent, so anything you create in the workspace won&#8217;t have any added color to the background unless you intentionally add it.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_GD_Gimp_-_Gimp_3-600px3.jpg\" alt=\"MF-Gimp-3\" /></li>\n<li>This is a closer view of the <strong>Toolbox</strong>. The grouping of tools labeled #1 are your selection tools. You should experiment with all of these to get a feel for the individual uses of each. The grouping of tools labeled #2 are your movement tools. The grouping #3 are your drawing tools, including the <strong>Pencil</strong> and <strong>Eraser</strong> tools which you&#8217;ll use for creating pixel art.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_GD_Gimp_-_Gimp_4-600px4.jpg\" alt=\"MF-Gimp-4\" /></li>\n<li>This is an example of <strong>Layers</strong>. If you look at the pink box on the right, the Layers window shows the blue background and then another layer for the text. Adding layers is useful as it allows you to change, adjust, and manipulate individual parts of your project instead of treating it as one combined piece.<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_GD_Gimp_-_Gimp_5-600px5.jpg\" alt=\"MF-Gimp-5\" /></li>\n</ol>\n<p>It&#8217;s recommended that you use the basic skills from the overview and instruction and jump right into practicing manipulating Gimp. However, there are more resources for you to review and use:</p>\n<p>This is a great video with a solid overview of Gimp. It&#8217;s pulled from the Davis Media Design YouTube channel. If you&#8217;re going to use this video, it&#8217;s recommended to have Gimp open so that you can practice the skills while watching the video.</p>\n<p><iframe width=\"625\" height=\"352\" src=\"https://www.youtube.com/embed/P143GdVeMjE?feature=oembed\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe></p>\n"}},"pathContext":{}}

